import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Button from '@mui/material/Button';
import axios from 'axios';
import Modal from 'react-bootstrap/Modal';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import "../style/addProduct.css"
import { useState, useEffect } from 'react'

export default function AddProduct({ show, onClose }) {
    const [file, setFile] = useState();
    const [productInfo, setProductInfo] = useState({
        isim: '',
        fiyat: '',
        aklama: '',
        kategoriID: ''
    });
    const [liste, setListe] = useState([]);
    const [Kategori, setKategori] = useState('');

    const handleFile = (e) => {
        const selectedFile = e.target.files[0];
        console.log('Selected File:', selectedFile);
        setFile(selectedFile);
        console.log(file);
    }

    useEffect(() => {
        axios.get("http://localhost:8081/getAllCategories")
            .then(res => {
                setListe(res.data);
            })
            .catch(err => console.log(err))
    }, []);

    const successToast = () => {
        toast.success('Product is added successfully !', {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light"
        });
    }

    useEffect(() => {
        if (file) {
            const formData = new FormData();
            formData.append("image", file);
            console.log(formData);
        }
    }, [file]);

    const save = () => {
        successToast();
        axios.post("http://localhost:8081/setProduct", productInfo)
            .then(res => {
                const formData = new FormData();
                console.log(file);
                formData.append("image", file);
                console.log(res.data);
                axios.post(`http://localhost:8081/uploadProductImg/${res.data.ProductId}`, formData)
                    .then(uploadRes => {
                        console.log(uploadRes.data);
                    })
                    .catch(uploadErr => console.log(uploadErr));
            })
        .catch(err => console.log(err)); 
        onClose();
    }

    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setProductInfo((prevInfo) => ({
            ...prevInfo,
            [name]: value
        }));
    };

    const handleKategoriChange = (event) => {
        const kategoriID = event.target.value;
        setKategori(kategoriID);
        setProductInfo((prevInfo) => ({
            ...prevInfo,
            kategoriID: kategoriID
        }));
    };

    return (
        <>
            <Modal show={show} onHide={onClose}>
                <Modal.Body>
                    <div className='center'>
                        <Box
                            component="form"
                            sx={{
                                '& > :not(style)': { m: 1, width: '25ch' },
                            }}
                            noValidate
                            autoComplete="off"
                        >
                            <input type='file' className='form-control' onChange={handleFile} />
                            <TextField
                                id="productName"
                                label="rn smi"
                                variant="outlined"
                                name="isim"
                                value={productInfo.isim}
                                onChange={handleInputChange}
                            />
                            <TextField
                                id="productPrice"
                                label="rn Fiyat"
                                variant="outlined"
                                name="fiyat"
                                value={productInfo.fiyat}
                                onChange={handleInputChange}
                            />
                            <TextField
                                id="productDescription"
                                label="rn Aklamas"
                                variant="outlined"
                                name="aklama"
                                value={productInfo.aklama}
                                onChange={handleInputChange}
                            />
                            <FormControl fullWidth>
                                <InputLabel id="demo-simple-select-label">rn Kategorisi</InputLabel>
                                <Select
                                    labelId="demo-simple-select-label"
                                    id="demo-simple-select"
                                    label="rn Kategorisi"
                                    name="kategoriID"
                                    value={Kategori}
                                    onChange={handleKategoriChange}
                                >
                                    {liste.map((kategori) => (
                                        <MenuItem key={kategori.id} value={kategori.id}>
                                            {kategori.kategoriName}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                            <Button className='saveButton' variant="outlined" onClick={() =>{
                                save();
                            }}>save</Button>

                        </Box>
                    </div>
                </Modal.Body>
            </Modal>
            <ToastContainer />
        </>
    );
}
